                  style="height: 1.625rem; width: 1.625rem"
                  src="~@/assets/icon/choose.png"
                  alt="choose"
                />
              </span>
              <span
                class="remove-file"
                @click="deleteFiles(item.attributeId, item)"
              >
                <img
                  src="~@/assets/icon/delete.png"
                  alt="delete"
                />
              </span>
            </div>
          </div>
          <div
            v-for="file in files"
            class="file-listing"
            :key="file.key"
          >
            <img
              class="file-img"
              src="~@/assets/icon/file.png"
              alt="file"
            />
            <p>{{ file.name }}</p>
            <div class="cover-file">
              <span
                class="choose-file"
                @click="submitFiles(file)"
                v-if="chooseImg == 1"
              >
                <img
                  style="height: 1.625rem; width: 1.625rem"
                  src="~@/assets/icon/upload.png"
                  alt="upload"
                />
              </span>
              <span
                class="choose-file"
                v-if="chooseImg == 2"
              >
                <a-icon
                  type="sync"
                  spin
                />
              </span>
              <span
                class="choose-file"
                @click="triggerChooseFiles(item.attributeId)"
                v-if="chooseImg == 0"
              >
                <img
                  style="height: 1.625rem; width: 1.625rem"
                  src="~@/assets/icon/choose.png"
                  alt="choose"
                />
              </span>
              <span
                class="remove-file"
                @click="removeFile(file)"
              >
                <img
                  style="height: 1.625rem; width: 1.625rem"
                  src="~@/assets/icon/remove.png"
