                />
              </span>
            </div>
          </div>
          <button
            class="add-files"
            @click="addFiles()"
          >+</button>
        </div>
      </div>
    </div>

    <div class="content">
      <!-- 中间内容 -->
      <div class="gutter-box-content">
        <!-- table display (Component nesting) 添加组件功能在该组件中 -->
