          :tableHeader="attributesName"
          :tableData="expsAttributes"
          :attributeInfo="attributeInfo"
          @getLabels="getLabels"
        ></get-table>
      </div>
      <!-- 右固定 -->
      <div class="right-button">
        <!-- (only) label display -->
        <div class="label-button">
          <h1>Label</h1>
          <div
            class="line"
            style="width: 88%"
          >
            <div class="cover-content">
              <div class="cover-label">
                <a-spin
                  tip="Loading..."
                  :spinning="loadding"
                  size="large"
                >
                  <template v-for="(item, index) in labels">
                    <a-tooltip
                      v-if="item.labelName.length > 20"
                      :key="index"
                      :title="item.labelName"
                    >
                      <a-tag
                        :key="index"
                        :closable="index !== idx"
                        :afterClose="
                          () => labelClose(item.labelName, item.labelId)
                        "
                        :color="colors[index]"
                      >{{ `${item.labelName.slice(0, 20)}...` }}</a-tag>
                    </a-tooltip>
                    <a-tag
                      style="margin-top: 0.3rem"
                      v-else
                      :key="'a'+index"
                      :closable="index !== idx"
                      :afterClose="
                        () => labelClose(item.labelName, item.labelId)
                      "
                      :color="colors[index]"
                    >{{ item.labelName }}</a-tag>
                  </template>
                </a-spin>
              </div>
            </div>
          </div>
        </div>
        <!-- over -->

        <!-- add subproject and subproject display -->
        <div class="group-button">
          <h1>Subproject</h1>
          <div
