          ></div>
          <div
            class="group-button-content"
            v-if="isGroup"
          >
            <div class="group-list">
              <div
                v-for="(item, value) in groupInfo"
                :key="value"
              >
                <div class="group-list-item">
                  <span style="width: 40%; margin-left: 0.625rem">{{
                    item.name
                  }}</span>
                  <span style="width: 20%">{{ item.labels.length }}&nbsp;&nbsp;Label</span>
                  <span
                    style="
                      width: 5%;
                      margin-right: 0.625rem;
                      text-align: end;
                      cursor: pointer;
                    "
                    @click="deleteGroup(value, item.id)"
                  >×</span>
                </div>
              </div>
              <a-select
                mode="tags"
                class="select-input"
                style="width: 100%"
                @change="chooseLabel"
                placeholder="Choose Label"
              >
                <a-select-option
                  v-for="item in labels"
                  :key="item.labelName.toString(36)"
                >{{ item.labelName }}</a-select-option>
              </a-select>
              <input
                class="group-name"
                type="text"
                v-model="groupName"
              />
            </div>
            <div
