<script>
import axios from "axios";
import { mapState } from "vuex";
import getTable from "./CreateLabel/getTable";
import projectId from '../../../../store/modules/projectId';

export default {
  name: "CreateLabel",

  components: {
    getTable,
  },

  data() {
    return {
      // 上传文件
      files: [],
      attributeFiles: [],
      source: null,
      chooseImg: "1",
      checkKey: 1,
      isRemove: true,
      //信息
      attributeInfo: [],
      expsAttributes: [],
      attributesName: [],
      expName: [],
      colors: [
        "#00c2b2",
        "#B6C7F0",
        "#85A0DD",
        "#5F7EC6",
        "#4061AD",
        "#264A9C",
        "#FFE8BA",
        "#FFDA8F",
        "#FFCE6B",
        "#FFC34C",
        "#E9A825",
        "#FFD9BA",
        "#FFC18F",
        "#FFAE6B",
        "#FF9D4C",
        "#E97E25",
        "#ACEDED",
        "#77D5D5",
        "#4EBABA",
        "#2E9C9C",
        "#168C8C",
        "#ff461f",
        "#ff2d51",
        "#f36838",
        "#ed5736",
        "#ff4777",
        "#f00056",
        "#ffb3a7",
        "#f47983",
        "#db5a6b",
        "#c93756",
        "#f9906f",
        "#f05654",
        "#ff2121",
        "#f20c00",
        "#8c4356",
        "#c83c23",
        "#9d2933",
        "#ff4c00",
        "#ff4e20",
        "#f35336",
        "#dc3023",
        "#ff3300",
        "#cb3a56",
        "#a98175",
        "#b36d61",
        "#ef7a82",
        "#ff0097",
        "#c32136",
        "#be002f",
        "#c91f37",
        "#bf242a",
        "#c3272b",
        "#9d2933",
        "#60281e",
        "#622a1d",
      ],
      // 当前file的id
      attributeId: null,

      idxs: -1,
      idx: "",
      //filter条件
      tags: ["Screening conditions:"],
      isTag: "",
      type: "",
      field: "ExpName",
      //复选框
      checkedNames: [],
      checkedArr: [],
      checkIndex: "",
      filterValue: {
        field: "",
        type: "string",
        contain: "",
      },
      //数字输入框
      textNumberMax: "",
      textNumberMin: "",
      //string输入框
      textString: "",
      contain: [],
      filtersData: { filters: [] },
      //添加Label
      isAddLabel: false,
      labelData: {
        label: {
          name: "",
          filters: [],
          expNames: [],
        },
      },
      textExp: "Exp036035/Exp036154",
      textLabel: "hello",
      //删除Label
      textDelete: "ha",
      // labels: [],
      //创建分组
      isGroup: true,
      groupName: "group name",
      groupInfo: [],
      labels: [],
      // 传递给组件的头部数据信息
      tableHeader: [],
      stateName: "template",
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      projectName: (state) => state.projectId.proInfo.projectName,
      // labels: (state) => state.Label.labels,
      loadding: (state) => state.folder.loadding,
    }),
  },

  mounted() {
    this.requestFileData();
  },

  methods: {
    getLabels(e) {
      this.labels = e;
    },
    // 请求属性表
    requestFileData() {
      var id = Number(this.projectId);
      this.$progress.start(); // 进度条加载
      this.$ProjectModels
        .getAttributeModels(id)
        .then((res) => {
          this.$progress.finish(); // 进度条完成
          this.attributeFiles = res.data.attributeFiles;
        })
        .catch((err) => {
          window.console.log(err);
          this.$progress.fail();
        });
    },

    // 本地属性表upload
    addFiles() {
      this.$refs.files.click();
    },

    //本地上传file
    handleFilesUpload() {
      console.log(111);
      let uploadedFiles = this.$refs.files.files;
      for (let i = 0; i < uploadedFiles.length; i++) {
        this.files.push(uploadedFiles[i]);
      }
    },

    //本地删除file
    removeFile(key) {
      this.isRemove = false;
      Array.prototype.remove = function (obj) {
        let index = this.indexOf(obj);
        if (index !== -1) this.splice(index, 1);
      };
      this.files.remove(key);
      this.chooseImg = 1;
      this.isRemove = true;
    },

    // 上传属性表至服务端
    submitFiles(key) {
      this.chooseImg = 2;
      let formData = new FormData();
      let file = key;
      formData.append("file", file);
      var id = this.projectId;
      this.$ProjectModels.postUploadModels(id, formData).then((res) => {
        this.chooseImg = 0;
        this.triggerChooseFiles(res.data);
      });
    },

    // 服务端选择file
    triggerChooseFiles(attributeId) {
      this.titleKey = "sub2";
      this.openKeys = ["sub2"];
      this.defaultSelectedKeys = ["1"];
      this.attributeId = attributeId;
      var id = Number(this.projectId);
      this.$progress.start(); // 进度条加载
      var data = {
        filters: [],
      };
      axios
        .all([
          this.$ProjectModels.getFilesDataModels(attributeId, id),
          this.$ProjectModels.postLabelExpModels(attributeId, id, data),
          this.$ProjectModels.getAllLabelModels(id),
          this.$ProjectModels.getGroupsInfoModels(id, attributeId),
        ])
        .then((res) => {
          this.attributeInfo = res[0].data;
          this.attributeInfo.label = {
            field: "label",
            idx: 999,
            type: res[0].data.names.type,
            width: 600,
          };
          this.expsAttributes = res[1].data.expsAttributes;
          this.attributesName = res[1].data.attributesName;
          this.labels = res[2].data.labels;
          // this.$store.commit("getlabels", res[2].data.labels);
          this.groupInfo = res[3].data.groups;

          this.$progress.finish(); // 进度条加载wanbi
        })
        .catch((error) => window.console.error("Error:", error));
    },

    //服务端删除file
    deleteFiles(attributeId, item) {
      var deleteKey = this.attributeFiles.indexOf(item);
      var id = Number(this.projectId);
      this.$ProjectModels
        .deleteFilesModels(attributeId, id)
        .then((ele) => {
          window.console.log(ele);
          this.attributeFiles.splice(deleteKey, 1);
        })
        .catch((error) => window.console.error("Error:", error));
    },

    //删除Label
    DeleteLabel() {
      // this.labelKey = key.key;
      document.body.parentNode.style.overflow = "hidden";
      var id = Number(this.projectId);
      this.$ProjectModels
        .getAllLabelModels(id)
        .then((res) => {
          window.console.log(res.data.labels);
          this.labels = res.data.labels;
          // this.$store.commit("getlabels", res.data.labels);
        })
        .catch((error) => window.console.error("Error:", error));
    },
    labelClose(removedLabel, labelId) {
      var id = Number(this.projectId);
      // console.log(id)
      this.$ProjectModels
        .deleteLabelModels(id, labelId)
        .then((ele) => {
          window.console.log(ele);
        })
        .catch((error) => window.console.error("Error:", error));
      const Labels = this.Labels.filter((Label) => Label !== removedLabel);
      this.Labels = Labels;
      // this.$store.commit("getlabels", Labels);
    },

    //添加label

    addLabels() {
      this.labelData.label.filters = this.filtersData.filters;
      this.labelData.label.name = this.textLabel;
      window.console.log(this.labelData);
      var attributeId = this.attributeId;
      var id = Number(this.projectId);
      var data = this.labelData;
      // console.log(this.attributeId)
      this.$ProjectModels
        .postAddLabelModels(attributeId, id, data)
        .then((res) => {
          // console.log(res)
          this.expWithLabels = res.data.expWithLabels;
          this.isAddLabel = false;
        })
        .catch((error) => window.console.error("Error:", error));
      this.tags = ["Screening conditions"];
    },

    // 更新label显示

    // 创建group
    addGroup() {
      // this.isGroup = false;
      var attributeId = this.attributeId;
      var id = Number(this.projectId);
      var data = {
        groupInfo: {
          name: this.groupName,
          labels: this.groupData,
        },
      };
      this.$ProjectModels
        .postAddGroupModels(id, data, attributeId)
        .then((res) => {
          window.console.log(res.data);
          this.groupInfo.push(res.data);
        });
    },
    chooseLabel(value) {
      this.groupData = value;
    },

    // 删除group
    deleteGroup(value, groupId) {
      var id = Number(this.projectId);
      // console.log(groupId)
      this.$ProjectModels.DeleteGroupModels(id, groupId).then(() => {
        this.groupInfo.splice(value, 1);
      });
    },
    //downloadTemplate
    downloadTemplate() {
      console.log(projectId.state.proInfo.projectId);
      this.$ProjectModels.getTemplate({
        projectId: projectId.state.proInfo.projectId
      }).then(eve => {
        window.console.log(eve);
        if (!eve) {
          return;
        }
        var filename = this.stateName;
        let url = window.URL.createObjectURL(eve.data);
        let link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute(
          "download",
          filename + "." + eve.data.type.slice(12)
        );
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        if (eve.data.length == 0) {
          this.$message.warning("No task");
        } else {
          this.$message.success("Get chart data successfully", 3);
        }
      })
    }
  },
};
</script>

