<script>
import { mapState } from "vuex";
// import componentLabel from "./ProjectMain/Label";

export default {
  name: "projectMain",
  props: {
    projectData: {
      type: Array,
    },
  },
  data() {
    return {
      arr: [], // project info
      projectInfo: {}, // project info
      isLabel: true, // project label
      isFrom: true, // project from
      tags: ["Unremovable", "Tag 2"], // project tag
      inputVisible: false, // project tag
      inputValue: "", // project tag
      isTag: true, // project tag
      isShare: true, // project share
      email: "", // project share
      deletevalue: "", //
      isDelete: true,
      projectTag: [],
    };
  },

  created() {
    this.arr = this.projectData; // project info
  },

  watch: {
    projectId() {
      // project info
      this.projectData.forEach((res) => {
        if (res.projectId == this.projectId) {
          this.projectInfo = res;
        }
      });
    },
    arr() {
      // project info
      this.arr.forEach((res) => {
        if (res.projectId == this.projectId) {
          this.projectInfo = res;
        }
      });
    },
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      projectName: (state) => state.projectId.proInfo.projectName,
    }),
  },

  mounted() {
    if (sessionStorage.getItem("workflowState")) {
      let obj = JSON.parse(sessionStorage.getItem("workflowState"));
      this.workflowId = obj.workflowId;
      this.nodesData = obj.nodesData;
      this.$store.commit("getWorkflowId", obj.workflowId);
    }
  },

  methods: {
    editDOI() {
      // project info
    },

    editdescription() {
      // project info
    },

    toggleLabel() {
      // project label
      this.isLabel = !this.isLabel;
    },

    toggleFrom() {
      // project from
      this.isFrom = !this.isFrom;
    },

    toggleShare() {
      // project share
      this.isShare = !this.isShare;
    },

    toggleTag() {
      // project tag
      this.isTag = !this.isTag;
    },

    toggleDelete() {
      // project tag
      this.isDelete = !this.isDelete;
    },

    handleClose(removedTag) {
      // project tag
      const tags = this.tags.filter((tag) => tag !== removedTag);
      this.tags = tags;
    },

    showInput() {
      // project tag
      this.inputVisible = true;
      this.$nextTick(function () {
        this.$refs.input.focus();
      });
    },

    handleInputChange(e) {
      // project tag
      this.inputValue = e.target.value;
    },

    handleInputConfirm() {
      // project tag
      const inputValue = this.inputValue;
      let tags = this.tags;
      if (inputValue && tags.indexOf(inputValue) === -1) {
        tags = [...tags, inputValue];
      }
      Object.assign(this, {
        tags,
        inputVisible: false,
        inputValue: "",
      });
    },

    shareData() {
      let projectId = this.projectId;
      let data = {
        email: this.email,
      };
      this.$progress.start(); // 进度条加载
      this.$ProjectModels
        .postShareProjectModels(projectId, data)
        .then((res) => {
          this.$progress.finish(); // 进度条完成
          if (res.status == 200) {
            this.$message.success("Share success.", 3);
          } else {
            this.$message.warning("Sharing failed." + res.status, 3);
          }
        })
        .catch((error) => {
          this.$progress.fail();
          this.$message.error(
            "Failed to update the data. Please try again or check the network." +
              error,
            5
          );
        });
      this.visible = false;
    },

    //delete逻辑
    error() {
      this.$message.error("Please fill in the correct project name.");
    },

    deleteClick() {
      if (this.deletevalue == this.projectName) {
        var id = this.projectId;
        // console.log(id)
        this.$progress.start(); // 进度条加载
        this.$ProjectModels.deleteProjectModels(id).then((res) => {
          window.console.log(res);
          this.$progress.finish(); // 进度条完成
          this.$router.push({ path: "/projectSet" });
          let obj = {
            id: null,
            name: "Choose project",
          };
          this.$store.commit("getProjectId", obj);
        });
      } else {
        alert("NO");
      }
    },
  },
};
</script>


