<template>
  <div id="progressBtn">
    <div class="progress-bar">
      <div class="bar-inner" @click="progressBtn">
        <div class="progress" :style="{width: progressWdith+'%'}"></div>
        <div class="progress-btn">submit</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'progressBtn',
  props:{
    isStatu:Boolean
  },
  data() {
    return {
      progressWdith: 0,
      isStart: false
    };
  },
  watch: {
    isStatu() {
      if (this.isStatu === true) {
        this.progressWdith = 0;
      }
    }
  },
  methods: {
    progressBtn() {
      let progressnuw = 0;
      var timer = setInterval(() => {
        progressnuw = progressnuw + 2.5;
        if (progressnuw >= 100) {
          clearInterval(timer);
          this.isStart = true;
          this.$emit('progress-bar', this.isStart)
        }
        this.progressWdith = progressnuw;
      }, 1);
    }
  }
};
</script>

<style lang="scss" scoped>
#progressBtn {
  .progress-bar {
    height: 40px;
    .bar-inner {
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      width: 180px;
      border-radius: 50px;
      overflow: hidden;
      height: 100%;
      cursor: pointer;
      color: #fff;
      font-weight: 600;
      background: #4ebaba;
      margin-top: 25px;
      .progress {
        position: absolute;
        height: 100%;
        background: #2e9c9c;
      }
      .progress-btn {
        position: absolute;
        height: 100%;
        z-index: 99;
        left: 50%;
        transform: translate(-50%);
        line-height: 40px;
      }
    }
  }
}
</style>