<!--
* @componmentName getTable
* @module "src\views\analysisTool\projectSetPage\components\CreateLabel\getTable.vue"
* @fileDescription 展示表格
* @author li
* @date 2020年8月18日
* @param {number} [attributeId] 当前表格file id
* @param {Object} [tableHeader] 表格头部数据
* @param {Object} [attributeInfo] 表格头部包含信息
* @param {Object} [tableData] 表格自定义样式信息
* @example 调用示例
* <get-table :attributeId="attributeId" :tableHeader="attributesName" :tableData="tableData" :attributeInfo="attributeInfo"></get-table>
 -->

<template>
  <div class="getTable">
    <h1>Property Info</h1>

    <div class="line" style="width: 94%"></div>

    <div class="box-show">
      <!-- 没选择时的界面 -->
      <div class="no-choose" v-if="tableData.length == 0">
        <br />
        <h1 style="text-align: center">Please select a property file.</h1>
      </div>

      <!-- 头部注释 -->
      <div class="condition-show" v-else>
        <!-- 筛选表格 -->
        <div class>
          <scroll-table :dataHeap="dataHeap" :filterStyle="true">
            <!-- 表格title展示 -->
            <template slot="tableTitle">
              <p style="font-size: 0.625rem; margin: 0.625rem">
                After selecting your filter criteria, your criteria will be
                displayed one by one below.
              </p>
            </template>

            <!-- 表格筛选条件展示 -->
            <template slot="showCondition" slot-scope="slot">
              <button
                v-show="!inputName"
                class="add-label-input spaceInRight"
                @click="showInput"
              >
                create label
              </button>
              <div class="input-name slideLeftReturn" v-show="inputName">
                <input
                  type="text"
                  placeholder="label name"
                  v-model="labelName"
                />
                <button class="" @click="createLabel(slot.filtersCondition)">
                  →
                </button>
              </div>
            </template>

            <!-- 表格头部展示 -->
            <template slot="tableHeader" slot-scope="slot">
              <div>{{ slot.title }}</div>
            </template>

            <!-- 表格内容展示 -->
            <template slot="tableBox" slot-scope="slot">
              <div v-if="typeof slot.value == 'object'" style="display: flex">
                <div v-for="(label, key) in slot.value" :key="label">
                  <a-tag :color="colors[key]">{{ label }}</a-tag>
                </div>
              </div>
              <div v-else>
                <a-tooltip
                  v-if="slot.value.length > 13"
                  :key="slot.value"
                  :title="slot.value"
                  placement="top"
                >
                  <p style="white-space: nowrap">
                    {{ `${slot.value.slice(0, 13)}...` }}
                  </p>
                </a-tooltip>
                <p v-else>{{ slot.value }}</p>
              </div>
            </template>
          </scroll-table>
        </div>
      </div>
    </div>
  </div>
</template>  

<script>
// 颜色数组
import colors from "../../../../../utils/colors";
// 组件
import scrollTable from "../../../../../PublicComponment/scrollTable/scrollTable";
import { getStrWidth } from "../../../../../utils/tableWidth";
import { mapState } from "vuex";

export default {
  name: "getTable",

  components: {
    scrollTable,
  },

  props: {
    attributeId: {
      type: Number,
    },
    tableHeader: {
      type: Array,
    },
    tableData: {
      type: Array,
    },
    attributeInfo: {
      type: [Object, Array],
    },
  },

  data() {
    return {
      colors: [],
      tableStyle: {
        headerBackground: "",
        headerHeight: "",
        headerWidth: "",
        headerColor: "",
        tableHeight: "",
      },
      // 删除tag的元素
      removedTag: "",
      // 控制label name的输入显示
      inputName: false,
      // label name
      labelName: null,
    };
  },

  computed: {
    // 对表格数据进行修改
    dataHeap() {
      let arr_1 = [];
      this.tableHeader.forEach((item) => {
        let obj = {};
        for (var i in this.attributeInfo) {
          if (item == "label") {
            obj = {
              name: item,
              type: this.attributeInfo[i].type,
              width: 600,
            };
          } else {
            obj = {
              name: item,
              type: this.attributeInfo[i].type,
              filter: true,
            };
            if (getStrWidth(item) < 7) {
              obj.width = 140;
            } else if (getStrWidth(item) > 15) {
              obj.width = 300;
            } else {
              obj.width = getStrWidth(item) * 20;
            }
          }
          if (
            this.attributeInfo[i].category &&
            this.attributeInfo[i].category.length < 5
          ) {
            obj.type = "checkbox";
          }
          if (this.attributeInfo[i].type == "string") {
            obj.list = this.attributeInfo[i].category;
          }
          if (this.attributeInfo[i].type == "number") {
            obj.max = this.attributeInfo[i].max;
            obj.min = this.attributeInfo[i].min;
          }
          if (this.attributeInfo[i].type == "checkbox") {
            obj.list = this.attributeInfo[i].category;
          }
        }
        arr_1.push(obj);
      });

      let obj = {
        titleData: arr_1,
        contentData: [...this.tableData],
      };
      console.log("obj==>", obj);
      return obj;
    },
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      projectName: (state) => state.projectId.proInfo.projectName,
    }),
  },

  created() {
    // 获取颜色
    this.colors = colors.colors;
  },

  methods: {
    showInput() {
      this.inputName = !this.inputName;
    },

    createLabel(a) {
      let arr = [];
      a.forEach((item) => {
        if (item.type === "string" || item.type === "checkbox") {
          let obj = {
            field: item.name,
            type: "string",
          };
          obj[item.choice] = item.condition;
          arr.push(obj);
        } else if (item.type === "number") {
          arr.push({
            field: item.name,
            type: item.type,
            contain: item.condition,
          });
        }
      });
      let attributeId = this.attributeId;
      let id = Number(this.projectId);

      let labelData = {
        label: {
          filters: {},
        },
      };
      labelData.label.filters = arr;
      labelData.label.name = this.labelName;
      let data = labelData;
      this.$ProjectModels
        .postAddLabelModels(attributeId, id, data)
        .then((res) => {
          window.console.log(res);
          // this.$ProjectModels
          //   .getAllLabelModels(id)
          //   .then((res) => {
          //     window.console.log(res.data.labels);
          //     this.labels = res.data.labels;
          //   })
          //   .catch((error) => window.console.error('Error:', error));
        })
        .then((res) => {
          this.$ProjectModels.getAllLabelModels(id).then((res) => {
            // this.$store.commit("getlabels", res.data.labels);
            this.$emit("getLabels", res.data.labels);
          });
        })
        .then((res) => {
          this.$message.success("创建成功");
        })
        .catch((error) => window.console.error("Error:", error));
    },
  },
};
</script>
