              <a-tag :key="tag" :closable="index == index" >{{tag}}</a-tag>
            </template>
            <div class="add-tag">
              <span>{{messageTagData}}&nbsp;</span>
              <input ref="message" type="text" v-model="messageTagData" @keyup.enter="handleMessageData" @keyup.delete="handleMessageDelete">
