          <template v-for="(tag, index) in tags">
            <a-tooltip v-if="tag.expname.length > 20" :key="tag.expname" :title="tag.expname">
              <a-tag :key="tag.expname" :closable="index == index" :color="tag.from === 'local'? '#4EBABA' :'#5F7EC6'" :afterClose="() => handleClose(tag)">{{`${tag.expname.slice(0, 20)}...`}}</a-tag>
            </a-tooltip>
            <a-tag v-else :key="tag.expname" :closable="index == index" :color="tag.from === 'local'? '#4EBABA' :'#5F7EC6'" :afterClose="() => handleClose(tag)">{{tag.expname}}</a-tag>
          </template>
        </div>
      </div>

