        <form action id="demo">
          <div class="from-submit">
            <input type="text" placeholder="project Name" v-model="input1" />
            
            <br />
            <br />
            <p style="text-align: start">Description:</p>
            <textarea type="text" rows="10" placeholder="project Description" v-model="input2" class="input2" />
           
          </div>
          <progressBtn @progress-bar="submitUpdate" :isStatu="isStatu"></progressBtn>
