        <textarea placeholder="Please paste your lab number here, but please note that we need your lab to exist in our database. If you need to download additional data, please go to Firmianan." rows="10" v-model="message" value="this.projectInfoData.projectName" />
        <button @click="upTags" class="handleTags">Submit to the right</button>
