<script>
import progressBtn from '../../../../PublicComponment/progressBtn';
import antdTable from '../../../../PublicComponment/antdTable/antdTable';
import Cookies from 'js-cookie';
import { mapState } from 'vuex';

export default {
  name: 'changeProject',

  components: {
    progressBtn,
    antdTable,
  },

  data() {
    return {
      // firmiana数据
      tag_ipro: [],
      // local upload 数据
      tag_loc: [],
      // 自定义信息填入
      messageTagData: '',
      // 自定义tag数据分发给子组件暂存地址
      messageTags: [],

      initialTags:[],

      // DataFromData
      DataFromData:[],
      projectInfoData:{},

      // 提交数据
      input1: '',
      input2: '',
      projectInfo: {
        projectName: '',
        projectDescription: '',
        exps: [],
      },

      isStatu:false
    };
  },

   computed: {
    // 数据合并
    tags() {
      let arr = [...this.tag_ipro, ...this.tag_loc];
      return arr;
    },

    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      projectName: (state) => state.projectId.proInfo.projectName,
    }),
  },

  created() {
    this.input1 = this.projectName;
    this.input2 = this.projectInfoData.projectDescription; // 预设项目名称和项目描述
  },

  mounted() {
    //请求用户所有实验
    this.requestAllProject(this.projectId);
  },

  methods: {
    // 获取组件中传递的firmiana实验数据
    tagData_ipro(e) {
      this.tag_ipro = e;
    },

    // 获取组件中传递的local upload 实验数据
    tagData_loc(e) {
      this.tag_loc = e;
    },

    // 处理自定义写入实验数据
    handleMessageData() {
      let arr = this.messageTagData.split(','); //字符分割
      arr.forEach((element) => {
        if (element === '' || this.messageTags.indexOf(element) > -1) {
          return;
        }
        this.messageTags.push(element);
      });
      this.initialTags.push(this.messageTags)
      // 输入完成之后清除输入框
      this.messageTagData = '';
    },

    handleClose(removedTag) {
      if(this.tag_ipro.indexOf(removedTag) > 1){
        this.tag_ipro.splice(this.tag_ipro.indexOf(removedTag),1)
      }else if(this.tag_loc.indexOf(removedTag) > 1){
        this.tag_loc.splice(this.tag_loc.indexOf(removedTag),1)
      }
    },

    // 使用delete按键删除事件
    handleMessageDelete() {
      if (this.messageTagData === '') {
        // 输入框中没有任何数据的时候，删除数组中的最后一个元素。改变原数组！
        this.messageTags.pop();
      }
    },

    // 点击聚焦事件
    getFocus() {
      this.$refs.message.focus();
    },

    //from表单数据
    test1() {
      this.result1 = '创建' + this.input1 + ' 成功!';
    },
    test2() {
      this.result2 = '创建' + this.input2 + ' 成功!';
    },

    hanldeData() {
      if (this.input1 !== '') {
        this.projectInfo.projectName = this.input1;
      } else {
        this.projectInfo.projectName = this.projectInfoData.projectName;
      }
      if (this.input2 !== '') {
        this.projectInfo.projectDescription = this.input2;
      } else {
        this.projectInfo.projectDescription = this.projectInfoData.projectDescription;
      }
      this.projectInfo.exps = this.tags;
      let projectInfo = this.projectInfo;
      let id = this.projectInfoData.projectId;
      let iproteome_token = JSON.parse(Cookies.get('iproteome_token'));
      this.$progress.start(); // 进度条加载
      this.$ProjectModels.updateProjectModels(id, projectInfo, iproteome_token).then((res) => {
        window.console.log(res);
        this.$progress.finish();
        this.isStatu = true;
      });
    },

    submitUpdate(a) {
      if (a === true) {
        this.hanldeData();
      }
    },

    // 数据请求方法：
    requestAllProject(id) {
      this.$progress.start(); // 进度条加载
      this.$ProjectModels.getProjectModels(id).then((res) => {
        let e = res.data.projectInfo.exps;
        this.projectInfoData = res.data.projectInfo;
        e.forEach((col) => {
          this.initialTags.push(col.expname);
        });
        this.$progress.finish();
      });
    },
  },
};
</script>

