<template>
  <!-- 项目展示主页面 -->
  <div class="projectSet">
    <!-- 引入头部切换组件 -->
    <header class="header">
      <analyNav @toProject="toProject" :currentTab="currentTab">
        <template v-slot:navWorkflow>
          <template v-for="items in navWorkflow">
            <div
              class="workflow-line"
              :key="items.sign"
              @click="toWorkflow(items.sign)"
            >
              &nbsp;&nbsp;<span class="workflow-text">{{ items.title }}</span>
              <template v-if="items.sign != navWorkflow.length">
                <span>&nbsp;></span>
              </template>
            </div>
          </template>
        </template>
      </analyNav>
    </header>
    <!-- 结束 -->

    <!-- 页面主要内容 -->
    <div class="content">
      <!-- 左侧展示 -->
      <!-- <div class="silder-list">
        <DataSet :projectData="projectData" :loading="loading"></DataSet>
      </div> -->
      <div :class="[mainId != 3 ? 'main' : 'main_3']">
        <div class="search" v-if="mainId == 0">
          <input
            v-clickoutside="isResearch"
            type="text"
            placeholder="Search Project"
            v-model="value"
            ref="value"
            @focus="searchFocus"
          />
          <button v-if="isResearch" type="submit" @click="searchCancel">
            CANCEL
          </button>
          <button v-else type="submit">SEARCH</button>
        </div>
        <div class="search-result" v-if="mainId == 0">
          <ul class="data-table">
            <div v-if="loading" style="padding: 0 3.125rem 0 3.125rem">
              <a-skeleton
                avatar
                :paragraph="{ rows: 2 }"
                :loading="loading"
                active
              />
              <div style="margin-top: 2.5rem"></div>
              <a-skeleton
                avatar
                :paragraph="{ rows: 2 }"
                :loading="loading"
                active
              />
              <div style="margin-top: 2.5rem"></div>
              <a-skeleton
                avatar
                :paragraph="{ rows: 2 }"
                :loading="loading"
                active
              />
            </div>
            <li
              v-for="item in searchData"
              :key="item.id"
              class="vanishIn time"
              v-else-if="isNoData"
            >
              <div class="data-show">
                <div class="DOI-link">
                  <i>{{ item.idx }} </i>
                </div>
                <div class="data-name">
                  <span
                    class="data-name-name"
                    @click="jumpProInfo(item.projectId, item.name)"
                    >{{ item.name }}</span
                  >
                  <p>
                    Created at
                    <span>{{ item.createDate }}</span>
                  </p>
                </div>
                <div class="data-description">
                  <p>
                    {{ item.description }}
                  </p>
                </div>
              </div>
              <div class="arrow">
                <!-- 打开项目信息按钮 -->
                <div
                  class="arrow-btn"
                  @click="jumpProInfo(item.projectId, item.name)"
                >
                  <a-tooltip placement="topRight">
                    <template slot="title">
                      <span>Project Info</span>
                    </template>
                    <icon-font type="icon-info-circle" />
                  </a-tooltip>
                </div>

                <!-- 打开编辑按钮 -->
                <div
                  class="arrow-btn"
                  @click="jumpChaPro(item.projectId, item.name)"
                >
                  <a-tooltip placement="topRight">
                    <template slot="title">
                      <span>Edit Project</span>
                    </template>
                    <icon-font type="icon-edit" />
                  </a-tooltip>
                </div>

                <!-- 打开label按钮 -->
                <div
                  class="arrow-btn"
                  @click="jumpCreLab(item.projectId, item.name)"
                >
                  <a-tooltip placement="topRight">
                    <template slot="title">
                      <span>Group Experiments</span>
                    </template>
                    <icon-font type="icon-ungroup" />
                  </a-tooltip>
                </div>

                <!-- 分享项目按钮 -->
                <div
                  class="arrow-btn"
                  @click="openShare(item.projectId, item.name)"
                >
                  <a-tooltip placement="topRight">
                    <template slot="title">
                      <span>Share Project</span>
                    </template>
                    <icon-font type="icon-share" />
                  </a-tooltip>
                </div>

                <!-- 删除项目按钮 -->
                <div class="arrow-btn">
                  <a-popconfirm
                    placement="topRight"
                    ok-text="Yes"
                    cancel-text="No"
                    @confirm="confirm(item.projectId)"
                  >
                    <template slot="title">
                      <p>
                        Are you sure you want to delete
                        <span style="color: #264a9c">{{ item.name }}</span>
                        ?
                      </p>
                    </template>
                    <a-tooltip placement="topRight">
                      <template slot="title">
                        <span>Delete Project</span>
                      </template>
                      <icon-font type="icon-delete1" />
                    </a-tooltip>
                  </a-popconfirm>
                </div>
              </div>
            </li>
            <div v-else class="no-data">NO DATA</div>
          </ul>
        </div>
        <div class="search-tool" v-if="mainId == 1">
          <projectMain :projectData="projectData"></projectMain>
        </div>
        <div class="search-tool" v-if="mainId == 2">
          <changeProject></changeProject>
        </div>
        <div v-if="mainId == 3">
          <CreateLabel>
            <!-- <template slot="toReturn">
              <div class="return-home">
                <a-button type="primary" @click="returnHome">
                  <a-icon type="left" />Go back
                </a-button>
              </div>
            </template> -->
          </CreateLabel>
        </div>
      </div>
    </div>

    <!-- 模态框 -->
    <a-modal
      title="Sharing"
      :visible="visible_share"
      :confirm-loading="loading_share"
      @ok="handleOk_share"
      @cancel="handleCancel_share"
    >
      <div class="single">
        <input
          v-model="email"
          style="
            border-radius: 0.625rem;
            outline: none;
            padding: 0.125rem 0.625rem 0.125rem 0.9375rem;
            border: 0.0625rem dashed;
            font-size: 0.875rem;
            width: 100%;
          "
          placeholder="User email"
        />
      </div>
    </a-modal>

    <div class="footer">
      <analyFooter></analyFooter>
    </div>
    <a-back-top style="right: 3.125rem" />
  </div>
</template>

<script>
import analyNav from "../../../PublicComponment/analyNav";
// import DataSet from './components/DataSet';
import analyFooter from "../../../PublicComponment/analyFooter";
import { Icon } from "ant-design-vue";
const IconFont = Icon.createFromIconfontCN({
  scriptUrl: "//at.alicdn.com/t/font_1429561_3jddrhu1fpu.js",
  extraCommonProps: {
    color: "#fff",
  },
});
import { mapState } from "vuex";
import projectMain from "./components/projectMain";
import changeProject from "./components/changeProject";
import CreateLabel from "./components/CreateLabel";
import clickoutside from "../../../plugins/directivesSet/clickoutside";
export default {
  name: "projectSet",

  inject: ["reload"],

  components: {
    IconFont,
    // DataSet,
    analyNav,
    analyFooter,
    projectMain,
    changeProject,
    CreateLabel,
  },

  data() {
    return {
      id: "null",
      currentTab: "projectSet",
      // 存储项目信息
      projectData: [],
      // 模块切换状态码
      mainId: 0,
      // 占位框的loading
      loading: true,
      // actives: 'generating',
      isResearch: false,
      value: "",
      isNoData: true,
      searchData: [],
      // 分享的数据
      visible_share: false,
      loading_share: false,
      email: "",
    };
  },

  directives: {
    clickoutside,
  }, // 自定义指令

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      projectName: (state) => state.projectId.proInfo.projectName,
      navWorkflow: (state) => state.titleWorkflow.navWorkflow,
    }),
  },

  created() {
    this.requestProjectData();
  },

  mounted() {
    this.reload;
    if (this.projectId == null) {
      let arr = [];
      arr.push(this.navWorkflow[0]);
      let obj = {
        title: "Project",
        sign: 2,
      };
      arr.push(obj);
      this.$store.commit("postNavWorkflowData", arr);
      this.mainId = 0;
    }
  },

  methods: {
    toWorkflow(e) {
      if (e == 2) {
        let arr = [];
        arr.push(this.navWorkflow[0]);
        let obj = {
          title: "Project",
          sign: 2,
        };
        arr.push(obj);
        this.$store.commit("postNavWorkflowData", arr);
        this.mainId = 0;
      }
    },
    searchCancel() {
      this.value = "";
      this.isResearch = false;
    },

    searchFocus() {
      this.isResearch = true;
    },

    searchClick(e, f) {
      let obj = {
        id: e,
        name: f,
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));
    },

    createProject() {
      // 切换创建项目组件
      let obj = {
        id: null,
        name: "Creating new project",
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));
      this.$router.push({ path: "/dataShow/" + 3 });
    },

    jumpProInfo(e, f) {
      // 切换项目信息组件
      this.mainId = 1;
      let obj = {
        id: e,
        name: f,
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));

      // 网页点击追踪
      let arr = [];
      arr.push(this.navWorkflow[0]);
      let obj_0 = {
        title: "Project",
        sign: 2,
      };
      arr.push(obj_0);
      let obj_1 = {
        title: f + "-" + "info",
        sign: 3,
      };
      arr.push(obj_1);
      this.$store.commit("postNavWorkflowData", arr);
    },

    jumpChaPro(e, f) {
      // 切换更改项目实验组件
      this.mainId = 2;
      let obj = {
        id: e,
        name: f,
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));

      // 网页点击追踪
      let arr = [];
      arr.push(this.navWorkflow[0]);
      let obj_0 = {
        title: "Project",
        sign: 2,
      };
      arr.push(obj_0);
      let obj_1 = {
        title: f + "-" + "edit",
        sign: 3,
      };
      arr.push(obj_1);
      this.$store.commit("postNavWorkflowData", arr);
    },

    jumpCreLab(e, f) {
      // 切换添加标签组件
      this.mainId = 3;
      let obj = {
        id: e,
        name: f,
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));

      // 网页点击追踪
      let arr = [];
      arr.push(this.navWorkflow[0]);
      let obj_0 = {
        title: "Project",
        sign: 2,
      };
      arr.push(obj_0);
      let obj_1 = {
        title: f + "-" + "group",
        sign: 3,
      };
      arr.push(obj_1);
      this.$store.commit("postNavWorkflowData", arr);
    },

    openShare(e, f) {
      this.visible_share = true;
      let obj = {
        id: e,
        name: f,
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));
    },

    handleCancel_share() {
      this.visible_share = false;
    },

    handleOk_share(i) {
      this.loading_share = true;
      let projectId = i;
      let data = {
        email: this.email,
      };
      this.$progress.start(); // 进度条加载
      this.$ProjectModels
        .postShareProjectModels(projectId, data)
        .then((res) => {
          this.$progress.finish(); // 进度条完成
          if (res.status == 200) {
            this.$message.success("Share success.", 3);
          } else {
            this.$message.warning("Sharing failed." + res.status, 3);
          }
          this.visible_share = false;
          this.loading_share = false;
        })
        .catch((error) => {
          this.$progress.fail();
          this.$message.error(
            "Failed to update the data. Please try again or check the network." +
              error,
            5
          );
        });
    },

    // 删除项目确认按钮
    confirm(i) {
      let id = i;
      // console.log(id)
      this.$progress.start(); // 进度条加载
      this.$ProjectModels.deleteProjectModels(id).then((res) => {
        this.$progress.finish(); // 进度条完成
        this.$router.push({ path: "/projectSet" });
        let obj = {
          id: null,
          name: "Choose project",
        };
        this.$store.commit("getProjectId", obj);
        if (res.status == 204) {
          this.$message.success("detele success.", 3);
        } else {
          this.$message.warning("Sharing failed." + res.status, 3);
        }
      });
    },

    //
    returnHome() {
      this.mainId = 0;
    },

    jumpTmpExt() {
      // 切换上传文件组件
      // if (this.projectId == null) {
      //   this.$notification[type]({
      //     message: 'reminder',
      //     description: 'You have not selected any item. Please select item and click again.',
      //   });
      // } else {
      this.mainId = 4;
      // }
    },

    //数据请求方法
    requestProjectData() {
      this.$progress.start(); // 进度条加载
      this.$ProjectModels.getProjectsModels().then((res) => {
        var data = res.data.projects;
        data.forEach((ele) => {
          this.projectData.push(ele);
        });
        // if (this.actives == 1) {
        //   var obj = {
        //     DOI: null,
        //     createDate: '未知',
        //     description: '未知',
        //     from: '未知',
        //     idx: 0,
        //     name: '正在处理数据中',
        //     privacy: '未知',
        //     projectId: 0,
        //     status: '未知'
        //   };
        //   this.projectData.push(obj);
        // }
        this.loading = false;
        this.isNoData = true;
        this.searchData = this.projectData;
        let dataProject = JSON.stringify(this.projectData);
        sessionStorage.setItem("dataProject", dataProject);
        this.$progress.finish(); // 进度条完成
      });
    },
    toProject(e) {
      this.mainId = e;
    },
  },

  watch: {
    value() {
      // 搜索框的监听
      if (this.value == "") {
        // 如果输入为空或者没有输入返回给全部数据
        this.searchData = JSON.parse(sessionStorage.getItem("dataProject"));
        return; // 直接返回
      }
      let list = []; // 创建临时数组,承接数据
      let listStorage = JSON.parse(sessionStorage.getItem("dataProject")); // 获取全部数据
      for (var i in listStorage) {
        // 循环
        if (
          listStorage[i].name.toLowerCase().indexOf(this.value.toLowerCase()) >=
          0
        ) {
          list.push(listStorage[i]); // 查找每一项中是否存在输入值,如存在则push.
        }
      }
      if (list.length == 0) {
        this.searchData = [{ name: "No data" }];
        // this.isResearch = false;
      } else {
        this.searchData = list;
        this.isResearch = true;
      }
    },
  },
};
</script>

